<template>
	<div class="chronology">
		<div class="chronology" v-if="inits.length">
			<div class="search">
				<input type="text" name="" placeholder="搜索国家/回合" v-model="filtertext">
				<button @click="changeheight()">筛选</button>
			</div>
			<div class="shaixuan">
				<h1>国家</h1>
				<ol>
					<li class="countries quxiao" v-for="(item,index) in countries" @click="countriesClick(item.CivilizationDescription,index)">
						{{item.CivilizationDescription}}
					</li>
				</ol>
				<h1>时代</h1>
				<ol>
					<li class="shidai quxiao" v-for="(item,index) in shidai" @click="shidaiClick(item.GameEra,index)">
						{{item.name}}
					</li>
				</ol>
				<h1 @click="quxiao()">取消</h1>
			</div>
			<ul v-if="initFilter.length">
				<li v-for="item in initFilter">
					<p>回合 {{item.Turn}}</p>
					<div class="title">
						<span>{{item.CivilizationDescription}}</span>
						<span>时代分数 {{item.EraScore}}</span>
					</div>
					<div class="cot">{{item.InstanceDescription}}</div>
				</li>
			</ul>
			<ul v-else>
				<li>
					<div class="title">
						<span>没有搜到内容诶，请搜索国家/回合...</span>
						<span>= &nbsp=</span>
					</div>
				</li>
			</ul>
		</div>
		<div class="animate" v-else>
			<img src="../assets/images/loading.gif">
		</div>
	</div>
</template>

<script>
	import axios from 'axios'

	let istrue = true

	export default{
		data(){
			return{
				filtertext:'',
				filterxuan:'',
				filterG:'',
				active:'',
				player:{},
				moment:{},
				inits:{},
				countries:{},
				shidai:[
					{
						name:'远古时代',
						GameEra:'ERA_ANCIENT'
					},
					{
						name:'古典时期',
						GameEra:'ERA_CLASSICAL'
					},
					{
						name:'中世纪',
						GameEra:'ERA_MEDIEVAL'
					},
					{
						name:'文艺复兴',
						GameEra:'ERA_RENAISSANCE'
					},
					{
						name:'工业时代',
						GameEra:'ERA_INDUSTRIAL'
					},
					{
						name:'现代',
						GameEra:'ERA_MODERN'
					},
					{
						name:'原子能时代',
						GameEra:'ERA_ATOMIC'
					},
					{
						name:'信息时代',
						GameEra:'ERA_INFORMATION'
					},
					{
						name:'未来时代',
						GameEra:'ERA_FUTURE'
					},
				]
			}
		},
		mounted(){
			axios
				.get('data.json')
				.then(res =>{
					this.moment = res.data.Moments;
					this.player = res.data.Players;
					this.$nextTick(() => {
						this.initchronlogy();
					})
				})		
		},
		methods:{
			initchronlogy(){
				let presult = this.player.map(item =>{
					return{
						'CivilizationDescription':item.CivilizationDescription,
						}
					});
				
				this.countries = presult.filter(item =>{
					return !item.CivilizationDescription.match('城') &&
						   !item.CivilizationDescription.match('野')
				})

				let mresult = this.moment.map(item =>{
					return{
						'Turn':item.Turn,
						'GameEra':item.GameEra,
						'ActingPlayer':item.ActingPlayer,
						'InstanceDescription':item.InstanceDescription
						}
					})


				let result = [];
			    for(var i=0;i<this.moment.length;i++){
			    	for (var j = 0; j < this.player.length; j++) {
			            if (this.moment[i].ActingPlayer == this.player[j].Id) {
			            	result.push({ 
			            		Turn: this.moment[i].Turn,
			            		GameEra: this.moment[i].GameEra,
			            		EraScore:this.moment[i].EraScore,
			            		ActingPlayer:this.moment[i].ActingPlayer,
			            		InstanceDescription: this.moment[i].InstanceDescription,
			            		CivilizationDescription: this.player[j].CivilizationDescription 
			            	});
			            }
			        }
			    }

			    let initMoment = result.filter(item =>{
					return !item.InstanceDescription.match('历史上曾有两军在此交战') 
						&& !item.InstanceDescription.match('此地曾发生海战')
				})

				this.inits = initMoment
			},
			changeheight(){

				let  shaixuan = document.querySelector('.shaixuan');
				let heights = '';

				if (istrue) {
					shaixuan.style.height = 'auto';
					heights = getComputedStyle(shaixuan).height;
					shaixuan.style.height = '0px';
					istrue = false
				}

				if (shaixuan.style.height == '0px') {
					shaixuan.style.height = heights	 
				}else{
					shaixuan.style.height = 0 
					istrue = true
				}
			},
			countriesClick(item,index){
				this.filterxuan = item;
				let a = document.querySelectorAll('.countries');
				
				for(var i = 0; i<a.length; i++){
					a[i].classList.remove('active');
				}
				a[index].classList.add('active');

			},
			shidaiClick(item,index){
				this.filterG = item;

				let b = document.querySelectorAll('.shidai');
				
				for(var i = 0; i<b.length; i++){
					b[i].classList.remove('active');
				}
				b[index].classList.add('active');
			},
			quxiao(){
				this.filterxuan = '';
				this.filterG = '';
				let c = document.querySelectorAll('.quxiao')

				for(var i = 0; i<c.length; i++){
					c[i].classList.remove('active');
				}
				
			}
		},
		computed:{
			initFilter(){
				return this.inits.filter(item =>{
					if (this.filtertext == '' && this.filterxuan == '' && this.filterG == '') {
						return this.inits
					}else if (this.filterxuan == '' && this.filterG == '') {
						return item.Turn == this.filtertext
					    || item.CivilizationDescription.match(this.filtertext)
					}else{
						return item.CivilizationDescription.match(this.filterxuan)
					       &&  item.GameEra == this.filterG
					}
					
				})
				
			},
			shaixuanFilter(){
				return this.inits.filter(item =>{
					return item.CivilizationDescription.match(this.filterxuan)
					&&  item.GameEra == this.filterG
					 
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.chronology{
		.search{
			width: 100%;
			height: 1rem;
			text-align: center;
			padding-top: .2rem;
			position:relative;
			background:#FF708C;
			input{
				width: 70%;
				height: .6rem;
				line-height: .6rem;
				text-align: center;
			}
			button{
				position: absolute;
				right: .3rem;
				height: .6rem;
				line-height: .6rem;
				background:none;
				color:white;
			}
		}
		.shaixuan{
			width: 100%;
			height: 0;
			overflow: hidden;
			background:#FF708C;
			color:white;
			transition: all .5s; 
			h1{
				font-size: .3rem;
				padding:0 0 0 calc(4% + .2rem);
				&:first-child{
					margin-top: .2rem;
				}
				&:last-child{
					margin-bottom: .2rem;
					height: .4rem;
					font-size: .29rem;
				}
			}
			ol{
				width: 100%;
				display: flex;
				flex-wrap: wrap;
				padding:4%;
				flex: 1;
				li{
					width: auto;
					padding:0 .2rem;
					height: .5rem;
					line-height: .5rem;
					font-size: .3rem;
					text-align: center;
					box-sizing:border-box;
					transition: .3s;
				}
			}
			.countries{
				
			}
			.shidai{
				
			}
			.active{
				border:.04rem solid white;
			}
		}
		ul{
			padding-top:.2rem;
			li{
				margin: 0 auto;
				margin-bottom: .2rem;
				width: 90%;
				height: auto;
				background:#FF708C;
				color:white;
				padding:.2rem;
				box-sizing: border-box;
				p{
					margin-bottom: .1rem;
					font-size: .26rem;
				}
				.title{
					margin-bottom: .2rem;
					span{
						&:last-child{
							float: right;
						}
					}
				}
				.cot{
					font-size: .28rem;
					margin-bottom: .1rem;
				}

			}
		}
		.animate{
			width: 100%;
			height: calc(100vh - .4rem - 1.8rem);
			display: flex;
			justify-content: center;
			align-items:center;
			img{
				width: 1rem;
				height: 1rem;
			}
		}
	}
</style>